import "core-js/modules/es.array.push.js";
import { useStore } from "vuex";
import { Toast } from "vant";
import "vant/es/toast/style";
import { useRouter } from "vue-router";
import { reactive, ref, toRefs } from "@vue/reactivity";
import { computed } from "vue";
export default {
  setup() {
    // 价格
    let price = ref(5050);
    let router = useRouter();
    let store = useStore();
    // 商品的数量
    let num = ref(1);
    // 返回上一级
    let goBack = () => {
      router.push({
        path: "/book"
      });
    };
    // 编辑与完成
    let booles = ref(true);
    let bianji = () => {
      booles.value = !booles.value;
      // console.log(booles.value);
    };
    // 新建一个空数组来接收请求的数据
    let data = reactive({
      shopData: []
    });
    // 获取数据
    let getData = () => {
      data.shopData = store.state.shoppingCar;
      data.shopData.forEach((item, index) => {
        data.shopData[index].bannerData["check"] = false;
        data.shopData[index].bannerData["num"] = 1;
        console.log(item);
      });
    };
    getData();
    // 点击确认支付
    const onSubmit = money => {
      //请选择商品判断是否勾选商品
      let havegoods = data.shopData.every(item => {
        //是否全为false，若没有一个沟的话就是true
        return item.bannerData.check == false;
      });
      //若没有一个沟的话就是true
      if (havegoods) {
        Toast("请勾选商品");
      } else {
        console.log(havegoods);
        Toast("提交成功");
        router.push({
          path: "/settlement"
        });
        //保存总价格
        window.localStorage.setItem("allmoney", JSON.stringify(money * 100 / 100));

        //执行功能
        store.commit("readyCarData");
      }
    };

    //全选按钮
    const allcheck = computed({
      set: v => {
        data.shopData.forEach(item => {
          item.bannerData.check = v;
        });
      },
      get: () => {
        return data.shopData.every(item => {
          return item.bannerData.check == true;
        });
      }
    });

    // 计算总价格
    let total = computed(() => {
      return data.shopData.reduce((total, item) => {
        // console.log(total);
        if (item.bannerData.check) {
          return total += item.bannerData.disprice * item.bannerData.num;
        } else {
          return total;
        }
      }, 0);
    });

    // 删除
    let del = () => {
      store.commit("del");
      getData();
    };
    return {
      goBack,
      ...toRefs(data),
      num,
      onSubmit,
      allcheck,
      price,
      total,
      booles,
      bianji,
      del
    };
  }
};